import React from 'react'

const Coin = ({name, price, symbol, volume, image, priceChange, marketcap}) => {
    return (
        <div className="coinsContainer">
            <div className="coinRow">
                <div className="coin">
                    <h1>{name}</h1>
                    <p className="coinSymbol">{symbol}</p>
                    <img src={image} alt="imagem da meda"></img>
                </div>
            </div>
            <div className="coinData">
                <p className="coinPrice">${price}</p>
                <p className="coinVolume">{volume.toLocaleString()}</p>
                <p className="coinMarket">Mkt cap: ${marketcap.toLocaleString()}</p>
            </div>
            {priceChange < 0 ? (
            <p className='coinPercent red'>{priceChange.toFixed(2)}%</p>
          ) : (
            <p className='coinPercent green'>{priceChange.toFixed(2)}%</p>
          )}
          



            
        </div>
    )
}

export default Coin
